body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.center {
  margin: auto;
  left: 0;
  right: 0;
}

.popup-bg-transparent .modal-content {
  background-color: transparent;
}

.pointer-none {
  pointer-events: none;
}

.no-scrollbar::-webkit-scrollbar {
  display: none;
}

@keyframes sparkle {
  0% {
    opacity: 0.5;
    transform: scale(0.8);
  }

  50% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}

@-webkit-keyframes sparkle {
  0% {
    opacity: 0.5;
    -webkit-transform: scale(0.8);
  }

  50% {
    opacity: 1;
    -webkit-transform: scale(1);
  }

  100% {
    opacity: 0.5;
    -webkit-transform: scale(0.8);
  }
}

@keyframes zoomInAndOut {
  0% {
    transform: scale(0.8);
  }

  50% {
    transform: scale(1);
  }

  100% {
    transform: scale(0.8);
  }
}

@-webkit-keyframes zoomInAndOut {
  0% {
    -webkit-transform: scale(0.8);
  }

  50% {
    -webkit-transform: scale(1);
  }

  100% {
    -webkit-transform: scale(0.8);
  }
}
.button {
  transition: 200ms ease;
  cursor: pointer;
 
}

.button:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 8px 1px rgba(255, 255, 255, 0.8);
}

.button-no-glow {
  transition: 200ms ease;
  cursor: pointer;
}

.button-no-glow:hover {
  transform: scale(1.1);
}

.glow-fx {
  box-shadow: 0px 0px 4px 1px rgba(255, 255, 255, 0.8);
}

a {
  text-decoration: none;
}

a:visited {
  color: inherit;
}

.cursor-pointer {
  cursor: pointer;
}
